.dropdown-container {
    text-align: left;
    border: 1px solid #ccc;
    position: relative;
    border-radius: 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  
  .dropdown-input {
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    -webkit-user-select: none;
            user-select: none;
  }

.dropdown-menu {
    position:absolute;
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
    width: 100%;

    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: auto;
    max-height: 100px;
    background-color: #fff;
}

.dropdown-item {
